export default {
// 此处编写的就是 vue组件实例的 配置项  通过一定语法  可以直接混入到组件内部
//   data methods computed 生命周期函数...
// 注意点：如果此处和组件内，提供了同名的data或methods 则组件内优先级更高
// 会用数组管理 统一执行
  data () {
    return {
      title: '标题'
    }
  },
  methods: {
    // 根据登录状态，判断是否需要显示登录确认框
    // 1.如果未登录=> 显示确认框 返回 true
    // 2.如果已登录=> 什么也不做 返回 false
    loginConfirm () {
      // 判断token是否存在
      // 1.如果token不存在，弹确认框
      // 2.如果token存在，继续操作
      if (!this.$store.getters.token) {
        // 弹确认框
        this.$dialog.confirm({
          title: '温馨提示',
          message: '此时需要先登录才能继续操作',
          confirmButtonText: '去登录',
          cancelButtonText: '再逛逛'
        })
          .then(() => {
            // 如果希望，挑战到登录=>登录后能跳回来
            // this.$touter.fullPath(会包含查询参数)
            // push跳转页面是在不断累加  而replace跳转页面不会累加 会替换掉之前的地址
            this.$router.replace({
              path: '/login',
              query: {
                backUrl: this.$router.fullPath
              }
            })
          })
          .catch(() => {})
        return true
      }
      return false
    }
  }
}
